const express = require('express');
const router = express.Router();
const Post = require('../models/Post'); // Correct relative path to the Post model

// Example route
router.get('/trending', async (req, res) => {
  try {
    const trendingPosts = await Post.find().sort({ likes: -1 }).limit(5); // Sort by likes in descending order
    res.json(trendingPosts);
  } catch (err) {
    res.status(500).json({ message: 'Error fetching trending posts', error: err.message });
  }
});


module.exports = router;
